/************************************************/
/*** CONSTANTS */

$header-bg-color: #fbfbfb;
$footer-bg-color: #323232;
$blue: #0090d1;
$black: #000;
$white: #fff;
$light-grey: #f7f7f7;
$medium-grey: #777;

$screen-sm-min: "only screen and (min-width: 768px)";

@font-face {
  font-family: Roboto;
  src: url('../fonts/Roboto-Light.woff');
  font-weight: normal; }

@font-face {
  font-family: Roboto;
  src: url('../fonts/Roboto-Medium.woff');
  font-weight: bold; }

@font-face {
  font-family: RobotoBold;
  src: url('../fonts/Roboto-Bold.woff'); }


/************************************************/
/*** MIXINS */

@mixin line-below($line-color: $black) {
  &::after {
    content: '';
    display: block;
    width: 80px;
    border-top: 3px solid $line-color;
    padding-top: 10px;
    margin-top: 20px; } }


/************************************************/
/*** GENERAL */

body, .ui * {
  font-family: Roboto; }


/************************************************/
/*** TOP NAV */

.ui {
  &.menu {
    background-color: $header-bg-color;
    border: none;
    box-shadow: none;
    img {
      height: 70px;
      margin-right: 50px; }
    .item {
      font-size: 1.1rem;
      &::before {
        content: none; }
      &.active {
        background-color: transparent;
        font-weight: bold; } } } }


/************************************************/
/*** MAIN CONTENT */

.main {
  padding-top: 20px; }

.page-title {
  margin-top: 10px;
  margin-bottom: 30px;
  font-weight: bold;
  font-size: 2.4rem; }

.table-of-contents {
  &.ui.list {
    margin-bottom: 50px;
    margin-left: 0;
    li {
      list-style: none;
      margin-bottom: 10px;
      &.active {
        a {
          color: $blue;
          font-weight: bold; } }
      &::before {
        content: none; }

      a {
        color: $black; } } } }


// ACCORDION

.ui.accordion {
  padding-left: 50px;
  .title:not(.ui) {
    font-size: 2rem;
    font-family: Roboto;
    position: relative;
    margin-bottom: 1rem;
    line-height: 1.2;
    &::before {
      display: block;
      content: '';
      width: 18px;
      height: 18px;
      position: absolute;
      left: -50px;
      background-image: url('../../assets/img/icon-chevron-right.svg');
      background-repeat: no-repeat;
      background-size: 100%;
      transition: transform 0.3s; }
    &.active {
      &::before {
        transform: rotate(90deg); } } }
  .content {
    margin-bottom: 2rem;
    p {
      font-weight: lighter;
      font-size: 1.2rem; } } }


// BLOG ITEMS

.items .ui.grid {
  .card {
    margin-bottom: 30px;
    background-color: $light-grey;
    position: relative;
    box-shadow: none;
    .date-overlay {
      position: absolute;
      background-color: $black;
      color: $white;
      top: 7px;
      left: 7px;
      padding: 7px 12px;
      font-size: 0.9rem;
      z-index: 10;
      line-height: 1; }
    .content {
      height: 100px;
      h3.header {
        margin-top: 5px;
        line-height: 30px;
        font-size: 1.2rem;
        font-weight: lighter;
        font-family: Roboto; } } } }

.ui.pagination.menu {
  background-color: $white;
  justify-content: center;
  .item {
    background-color: $white;
    border: 1px solid $medium-grey;
    border-radius: 3px;
    min-width: 0;
    width: 30px;
    height: 30px;
    font-size: 1rem;
    color: $medium-grey;
    padding: 0;
    margin: 0 10px;
    display: inline-block;
    line-height: 27px;
    &.active {
      border-top: 1px solid $medium-grey;
      padding-top: 0;
      color: $white;
      background-color: $blue;
      font-weight: lighter; } }
  .pagination-prev, .pagination-next {
    height: 30px;
    line-height: 30px;
    background-repeat: no-repeat;
    background-size: 10px;
    color: $black;
    text-transform: uppercase; }
  .pagination-prev {
    background-image: url('../../assets/img/icon-chevron-left.svg');
    background-position: left center;
    padding-left: 20px;
    margin-right: 20px; }
  .pagination-next {
    background-image: url('../../assets/img/icon-chevron-right.svg');
    background-position: right center;
    padding-right: 20px;
    margin-left: 20px; } }

.aside {
  h4 {
    text-transform: uppercase;
    color: $medium-grey;
    font-family: Roboto;
    font-weight: lighter;
    font-size: 1.4rem;
    margin-top: 10px;
    @include line-below(); }
  ul.ui.list {
    margin-left: 0;
    li {
      font-size: 1.1rem;
      border-bottom: 1px solid $light-grey;
      padding: 15px 0;
      &::before {
        content: none; } } } }


/************************************************/
/*** FOOTER */

.footer {
  background-color: $footer-bg-color;
  color: $white;
  margin-top: 30px;
  padding-top: 30px;
  padding-bottom: 30px;
  font-weight: lighter;
  .footer-section {
    margin-bottom: 70px;
    h5 {
      @include line-below($white); } }
  .logo {
    margin-top: 10px;
    margin-bottom: 20px;
    margin-right: 20px; }
  a {
    display: block;
    color: $white;
    margin-bottom: 15px; }
  form.ui {
    label {
      margin-top: 20px;
      font-size: 0.9rem;
      display: block; }
    input {
      &[type=text], &[type=text]:focus {
        background-color: $footer-bg-color;
        font-size: 0.9rem;
        color: $white;
        border: 1px solid $white;
        border-radius: 0;
        margin-top: 10px;
        padding-top: 12px;
        padding-bottom: 12px; } }
    button.ui {
      background-color: $white;
      color: $blue;
      font-weight: lighter;
      padding-top: 15px;
      padding-bottom: 15px;
      margin-top: 15px; } } }
